<template>
  <div class="row no-gutters justify-content-center">
    <div class="col-auto card list">
      <div class="card-header">
        メンバーリスト
      </div>
      <ul class="list-group">
        <li class="list-group-item list-group-item-action font-weight-bold h5 px-4" v-for="(member, key) in members" :key="`member-${key}`">
          <div class="row justify-content-between align-items-center">
            <div class="col-auto">{{ member }}</div>
            <div class="col-auto">
              <div class="btn btn-danger ml-5" @click="delMember(key)">X</div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    "members",
  ],
  data() {
    return {
      name: "members",
    }
  },
  methods: {
    delMember(key) {
      this.members.splice(key, 1);
    }
  }
}
</script>

