<template>
  <div class="result-btn text-center mt-4">
    <div class="btn btn-info h3" @click="getResult">発表順は...!</div>
  </div>
</template>

<script>
export default {
  props: [
    "members",
  ],
  data() {
    return {
      name: "getresult",
    }
  },
  methods: {
    getResult() {
      for (var i = this.members.length - 1; i >= 0; i--){
        // 0~iのランダムな数値を取得
        var rand = Math.floor( Math.random() * ( i + 1 ) );
        // 配列の数値を入れ替える
        [this.members[i], this.members[rand]] = [this.members[rand], this.members[i]];
      }
      this.$emit("set-now-loading", this.members)
    },
  }
}
</script>

