<template>
  <div class="main cotnainer">
    <Form ref="form" @get-members="getMembers" @reset-members="resetMember" @reset-result-members="resetResult" :result_members="result_members" />
    <Members v-if="members.length != 0" :members="members" />
    <NoMember v-if="members.length == 0 && result_members.length == 0 && !isShow" />
    <GetResult v-if="members.length > 1" @set-now-loading="setNowLoading" :members="members"  />
    <Result v-if="result_members.length != 0" :members="result_members" />
    <NowLoading v-show="isShow" :msg="loading_msg" />
  </div>
</template>

<script>
import Form from './MainComponents/Form.vue'
import Members from './MainComponents/Members.vue'
import NoMember from './MainComponents/NoMember.vue'
import GetResult from './MainComponents/GetResult.vue'
import Result from './MainComponents/Result.vue'
import NowLoading from './MainComponents/NowLoading.vue'

export default {
  data() {
    return {
      name: "main",
      members: [],
      result_members: [],
      isShow: false,
      loading_msg: "抽選中…",
    }
  },
  updated() {
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  },
  components: {
    Form,
    Members,
    NoMember,
    GetResult,
    Result,
    NowLoading,
  },
  methods: {
    getMembers(members) {
      this.members = members
      this.togleNowLoading(false)
    },
    setNowLoading(members) {
      this.$refs.form.resetFormAndMember()
      this.members = []
      this.togleNowLoading(true)
      var that = this
      setTimeout(function(){
        that.setResultMembers(members)
        that.togleNowLoading(false)
      }, 5000)
    },
    setResultMembers(members) {
      this.result_members = members
    },
    resetMember() {
      this.members = []
    },
    resetResult() {
      this.result_members = []
    },
    togleNowLoading(is_show) {
      this.isShow = is_show
    }
  },
}
</script>

<style lang="scss" scoped>
div.main {
  margin-top: 8rem;
  margin-bottom: 2rem;
}
</style>