<template>
  <header class="fixed-top text-center text-white bg-secondary">
    <h1 class="pt-3 mb-2">{{ title }}</h1>
    <p class="pb-3 mb-0 today">{{ today }}</p>
  </header>
</template>

<script>
export default {
  data() {
    return {
      name: "header",
      title: "LT発表順決定アプリ",
    }
  },
  computed: {
    today() {
      var weekDay = ["日", "月", "火", "水", "木", "金", "土"];
      var dt = new Date();
      var y = dt.getFullYear();
      var m = ("00" + (dt.getMonth()+1)).slice(-2);
      var d = ("00" + dt.getDate()).slice(-2);
      var wd = weekDay[dt.getDay()];
      var today = "今日の日付: " + y + "/" + m + "/" + d + " (" + wd + ")";
      return today;
    }
  },
}
</script>

