<template>
  <form class="form-row justify-content-center my-4">
    <div class="col-auto w-50">
      <input type="text" class="form-control name-form" placeholder="ニックネーム" v-model="newMember" @keyup.enter="addMember($event.keyCode)" >
      <!-- https://forum.framework7.io/t/vue-pressing-enter-key-in-input-causes-app-to-reload/2585/9 -->
      <input type="text" class="d-none" > <!-- input tag が 1つだけだとバグる -->
    </div>
    <div class="col-auto">
      <div class="btn btn-primary" @click="addMember(13)">追加</div>
    </div>
    <div class="col-12 d-flex mt-2" v-if="members.length != 0">
      <div class="btn btn-warning mx-auto d-none" @click="resetMember">リセット</div>
    </div>
  </form>
</template>

<script>
export default {
  props: [
    "result_members",
  ],
  data() {
    return {
      name: "form",
      members: [],
      newMember: "",
    }
  },
  methods: {
    addMember(keyCode) {
      if (keyCode !== 13) return
      if (!this.members.includes(this.newMember) && this.newMember != "") {
        this.members.push(this.newMember);
      }
      this.newMember = "";
      this.$emit('get-members', this.members);
      if (this.result_members.length != 0) {
        this.$emit('reset-result-members');
      }
    },
    resetMember() {
      this.members = [];
      this.$emit('reset-members');
    },
    resetFormAndMember() {
      this.newMember = "";
      this.resetMember();
    },
  },
}
</script>

